.bottom-menu {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    z-index: 11;
    &__in {
        align-items: center;
        justify-content: space-around;
        display: none;
        @media all and (max-width:900px) {
            display: flex;
        }
    }
    &__item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        grid-gap: 11px;
        padding: 9px 0;
        font-size: 8px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        color: #000;
        text-decoration: none;
    }
    &__item:active span {
        color: #F0820F;
    }
    &__item:active .bottom-menu__image svg path {
        stroke: #F0820F;
    }

    &__image {
        position: relative;
    }

    &__item-icon-counter {
        position: absolute;
        right: -9px;
        top: -3px;
        padding: 0 4px;
        background: #F0820F;
        color: #FFF;
        text-align: center;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        border-radius: 3px;
        box-shadow: 0px 0px 0px 2px #FFF;
    }
}