.info-for-delivery {
    display: flex;
    flex-wrap: wrap;
    gap: 15px 33px;
    align-items: flex-start;

    @media(width <= 960px){
        flex-direction: column;
        gap: 5px;
    }

    &__item {
        padding: 5px 3px;
        display: grid;
        grid-template-columns: max-content max-content max-content;
        gap: 5px;
        align-items: center;
        &--pay {
            grid-template-columns: max-content max-content;
            align-items: flex-start;

            @media(width <= 400px){
                grid-template-columns: max-content 1fr;
            }
