        font-weight: 500;
    }

    &__progress {
        flex: 1;
        height: 5px;
        background-color: #EEEEEE;
        border-radius: 3px;
        position: relative;
        overflow: hidden;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            border-radius: inherit;
            width: var(--es-progress);
            z-index: 1;
            background-color: #F0820F;
