        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }

    &__title {
        color: #000;
        font-size: 24px;
        font-weight: 500;
        sup {
            color: #909090;
            font-size: 14px;
            font-weight: 300;
            letter-spacing: 0.42px;
        }
    }

    &__add-review {
        padding: 0;
        margin: 0;
        background: none;
        border: none;
        color: #000;
        font-size: 17px;
        line-height: 1.3;
        border-radius: 7px;
        border: 2px solid #ECECEC;
        padding: 10px 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        cursor: pointer;
        transition: border-color 0.3s;
        max-width: 185px;
        width: 100%;

        @media(width <= 960px){
            position: fixed;
            bottom: 1rem;
            left: 50%;
