        border-radius: 6px;
        border: 1px solid #CED0D6;
        background: #FFF;
        padding: 15px 20px;
        width: 100%;
        box-sizing: border-box;
        resize: vertical;
        max-height: 500px;
    }

    textarea {
        min-height: 100px;
    }

    &__error {
        margin-top: 7px;
        color: #F0380F;
        font-size: 16px;
        font-weight: 500;
        display: none;
    }

    &__label-title {
        padding: 15px 0;
    }

    &__required {
        color: #F0380F;
    }

    &__footer {
        margin-top: 25px;

        @media(width <= 960px){
            display: flex;
            justify-content: center;
        }
    }

    &__btn-submit {
        border-radius: 4px;
        background: #F0820F;
        border: none;
        padding: 13px 25px;
        color: #fff;
        font-size: 17px;
        font-weight: 500;
        line-height: 1.3;
        cursor: pointer;

        @media(width <= 600px){
            flex: 1;
        }
    }
}

